@charset "UTF-8";

.header {
  display: block;
  width: 100%;
  margin: 0;
  padding-top: 17px;
  /*______design-project____*/
  /*  IE   */
}

.header__menu-container {
  display: block;
  max-width: 1163px;
  margin: 0 auto;
}

.header__logo {
  display: block;
  width: 182px;
  padding: 7px 13px;
  margin-right: 100px;
  float: left;
}

.header__burger {
  display: none;
  position: absolute;
  right: 16px;
  top: 20px;
  width: 30px;
  height: 24px;
  cursor: pointer;
  color: transparent;
  font-size: 0;
  background: url("../assets/menu-icon.png") 100%, no-repeat;
  background-size: cover;
  z-index: 1;
}

.header__checkbox {
  display: none;
}

.header__checkbox:checked + .header__burger {
  height: 30px;
  background: url("../assets/cancel.png") 100%, no-repeat;
  background-size: cover;
}

.header__checkbox:checked ~ .header__navigation {
  display: block;
}

.header__navigation {
  display: block;
  padding-left: 0;
  float: left;
  margin-top: 7px;
}

.header__link {
  display: inline-block;
  margin-right: 32px;
}

.header__link a {
  text-decoration: none;
  color: #333333;
  font-size: 16px;
  font-weight: 400;
  line-height: 36px;
  letter-spacing: 0.8px;
  border-bottom: 1px solid transparent;
}

.header__link a:hover {
  transition: 0.7s;
  color: #d81942;
  border-bottom: 1px solid #d81942;
}

.header__call-us {
  display: block;
  float: right;
  margin-bottom: 19px;
}

.header__number {
  color: #333333;
  font-size: 18px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0.9px;
  margin-top: 0;
  margin-bottom: 8px;
}

.header__callback {
  width: 157px;
  color: #333333;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.7px;
  border-radius: 16px;
  background-color: transparent;
  border: 1px solid #d81942;
  padding: 6px 19px 8px;
}

.header__callback:hover {
  transition: 0.5s;
  background-color: rgba(216, 25, 66, 0.51);
  color: whitesmoke;
  cursor: pointer;
}

.header__design-project {
  clear: both;
  display: block;
  width: 100%;
  min-height: 1017px;
  background: url("../assets/header-bg.png") 100%, no-repeat;
  background-size: cover;
  padding-bottom: 106px;
}

.header__banner {
  color: #151313;
  font-size: 50px;
  font-weight: 500;
  line-height: 52px;
  letter-spacing: 2.5px;
  text-align: center;
  padding-top: 103px;
  margin-top: 0;
  margin-bottom: 77px;
}

.header__we-offer {
  display: block;
  text-align: center;
  padding-left: 0;
  margin-bottom: 69px;
}

.header__offer-item {
  display: inline-block;
  width: 177px;
  color: #191818;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 0.9px;
  text-align: left;
}

.header__offer-item:before {
  content: "⚫";
  position: absolute;
  width: 13px;
  height: 13px;
  color: #d81942;
  margin-left: -24px;
}

.header__offer-item:nth-child(2) {
  width: 109px;
  margin-left: 101px;
  margin-right: 108px;
}

.header__offer-item:nth-child(3) {
  width: 207px;
  margin-right: 90px;
}

.header__calculate-cost {
  display: block;
  max-width: 572px;
  border-radius: 3px;
  background-color: #ffffff;
  padding-bottom: 36px;
  margin: 0 auto;
}

.header__indicate-area {
  color: #000000;
  font-size: 24px;
  font-weight: 500;
  padding-top: 40px;
  padding-left: 42px;
}

.header__range-value {
  display: block;
  width: 84px;
  height: 50px;
  font-size: 18px;
  font-weight: 300;
  letter-spacing: 0.9px;
  border-radius: 3px;
  border: 1px solid #bcd0d0;
  background-color: #f0f5f8;
  text-align: center;
  padding-top: 14px;
  margin-left: 41px;
  float: left;
}

.header__quad-meter {
  display: block;
  font-size: 18px;
  font-weight: 300;
  letter-spacing: 0.9px;
  float: left;
  margin: 12px 34px 0 15px;
}

.header #rangeNumber {
  background: -webkit-linear-gradient(left, #d71a41 0%, #d71a41 24%, #f0f5f8 24%, #f0f5f8 100%);
}

.header__range-container input {
  -webkit-appearance: none;
  width: 335px;
  height: 10px;
  border-radius: 5px;
  background-color: #f0f5f8;
  outline: none;
  margin-top: 17px;
}

.header__range-container input::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  height: 10px;
  border-radius: 5px;
  color: #d71a41;
  margin-top: -1px;
}

.header__range-container input::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 30px;
  height: 30px;
  box-shadow: 5px 2px 15px 1px rgba(0, 0, 0, 0.17);
  border-radius: 50%;
  border: 1px solid #ffffff;
  background-color: #ffffff;
  cursor: pointer;
  margin-top: -10px;
}

.header__range-container input::-webkit-slider-thumb:before {
  display: block;
  width: 100px;
  height: 100px;
  content: url("../assets/map-logo.png");
}

.header__range-container input[type=range] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  /* FF */
  width: 300px;
  /* FF */
  padding: 0;
  /* IE */
}

.header__range-container input[type=range]:focus {
  outline: none;
}

.header__range-container input[type="range"]::-moz-range-progress {
  background-color: #d71a41;
}

.header__range-container input[type="range"]::-moz-range-track {
  background-color: #f0f5f8;
}

.header__range-container input[type="range"]::-ms-fill-lower {
  background-color: #d71a41;
}

.header__range-container input[type="range"]::-ms-fill-upper {
  background-color: #f0f5f8;
}

.header__slider::-ms-track {
  -moz-appearance: none;
  width: 335px;
  height: 10px;
  box-shadow: inset 2px 1px 5px rgba(0, 0, 1, 0.1);
  border-radius: 5px;
  border: 1px solid #d9e3e9;
  background-color: #d71a41;
  background: -moz-linear-gradient(left, #d71a41 0%, #d71a41 24%, #f0f5f8 24%, #f0f5f8 100%);
  outline: none;
  clear: left;
  margin-top: 17px;
}

.header__slider::-ms-thumb {
  -moz-appearance: none;
  width: 30px;
  height: 30px;
  box-shadow: 5px 2px 15px 1px rgba(0, 0, 0, 0.17);
  border-radius: 50%;
  border: 1px solid #ffffff;
  background-color: #ffffff;
  cursor: pointer;
  margin-top: -14px;
}

.header__slider::-ms-fill-lower {
  background: #d71a41;
}

.header__slider::-ms-fill-upper {
  background: #f0f5f8;
}

.header__choice-parametres {
  display: block;
  padding-left: 41px;
}

.header__choice-parametres label {
  display: inline-block;
  font-size: 18px;
  font-weight: 300;
  margin-right: 57px;
  margin-left: 10px;
}

.header__choice-parametres input {
  display: none;
}

.header__choice-parametres i {
  display: inline-block;
  width: 22px;
  height: 22px;
  box-shadow: inset 2px 1px 5px rgba(0, 0, 1, 0.1);
  border-radius: 11px;
  background-color: #f0f5f8;
  position: relative;
}

.header__choice-parametres input[type="radio"] + i {
  border-radius: 50%;
  top: 4px;
  right: 10px;
}

.header__choice-parametres i:before {
  content: "";
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
  background-color: #d71a41;
  transform: scale(0);
  transition: 0.4s;
}

.header__choice-parametres input[type="radio"] + i:before {
  border-radius: 50%;
}

.header__choice-parametres input:checked + i:before {
  transform: scale(1);
}

.header__indicate-quantity-rooms,
.header__indicate-package {
  color: #000000;
  font-size: 24px;
  font-weight: 500;
}

.header__indicate-quantity-rooms.-bottom,
.header__indicate-package.-bottom {
  margin-bottom: 14px;
}

.header__package-label {
  clear: left;
  float: left;
  margin-bottom: 13px;
}

.header__package-label:last-child {
  margin-bottom: 15px;
}

.header__description-package {
  display: block;
  width: 264px;
  height: 188px;
  border-radius: 3px;
  background-color: #f0f5f8;
  margin-top: -6px;
  margin-left: 222px;
  position: absolute;
}

.header__package-on {
  color: #000000;
  font-size: 16px;
  font-weight: 500;
  margin-top: 19px;
  margin-left: 21px;
}

.header__package-on-box {
  margin-left: 22px;
}

.header__package-on-box p {
  color: #000000;
  font-size: 16px;
  font-weight: 300;
  line-height: 16px;
  white-space: pre;
  margin: 8px 0;
}

.header__triangle-svg-left {
  position: absolute;
  margin-top: -41px;
  margin-left: -33px;
}

.header__submit-container {
  clear: both;
  display: block;
}

.header__submit-container button {
  display: block;
  border-radius: 27px;
  color: #fefefe;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.8px;
  background-color: #d71a41;
  border: none;
  padding: 20px 37px;
  margin: 0 auto;
}

.header__submit-container button:hover {
  transform: scale(0.9);
  transition: 0.7s;
  cursor: pointer;
}

@media all and (max-width: 1165px) {
  .header__navigation {
    float: right;
    margin-bottom: 0;
  }

  .header__call-us {
    margin: 5px 32px 12px 0;
    float: right;
    clear: left;
  }

  .header__number {
    float: left;
    margin-right: 20px;
  }

  .header__callback {
    width: 133px;
    font-size: 12px;
    letter-spacing: 0.6px;
    padding: 11px 14px 12px;
    margin-top: 5px;
  }
}

@media all and (max-width: 1000px) {
  .header__we-offer {
    display: none;
  }
}

@media all and (max-width: 899px) {
  .header {
    padding-top: 0;
  }

  .header__navigation {
    display: none;
    max-width: 111px;
    float: none;
    text-align: center;
    clear: left;
    margin-left: calc(50% - 55px);
  }

  .header__burger {
    display: block;
  }

  .header__link {
    display: block;
    margin-right: 0;
  }
}

@media all and (max-width: 640px) {
  .header__design-project {
    background: none;
    min-height: 701px;
    border-top: 1px solid #dbe6e7;
    padding-bottom: 0;
  }

  .header__banner {
    max-width: 300px;
    font-size: 36px;
    letter-spacing: 1.8px;
    line-height: 40px;
    padding-top: 29px;
    margin: 0 auto;
  }

  .header__range-container {
    font-size: 16px;
  }

  .header__range-container input {
    width: 290px;
    margin-top: 23px;
  }

  .header__indicate-area {
    font-size: 16px;
    padding-top: 0;
    padding-left: 14px;
    margin-bottom: 21px;
  }

  .header__range-value {
    float: none;
    margin-left: 14px;
  }

  .header__quad-meter {
    display: none;
  }

  .header__slider {
    margin-left: 14px;
  }

  .header__choice-parametres {
    max-width: 390px;
    padding-left: 14px;
    margin-top: 25px;
  }

  .header__rooms-quantity {
    font-size: 16px;
  }

  .header__rooms-quantity label:nth-child(3) {
    margin-right: 133px;
    margin-bottom: 12px;
  }

  .header__indicate-quantity-rooms {
    font-size: 16px;
  }

  .header__indicate-package {
    font-size: 16px;
  }

  .header__description-package {
    display: none;
  }
}

@media all and (max-width: 320px) {
  .header__navigation {
    display: none;
    max-width: 111px;
    float: none;
    text-align: center;
    clear: left;
    margin-left: calc(50% - 55px);
  }

  .header__call-us {
    margin-top: 0;
    margin-right: 13px;
  }

  .header__number {
    font-size: 16px;
    margin-top: 0;
  }

  .header__callback {
    margin-top: 0;
  }
}

